body {
  background-color: #222222;
  font-family: "Odibee Sans", cursive; }

.container {
  min-height: 100vh;
  background: #141414; }

.pagewrapper {
  padding-top: 128px;
  min-height: calc(100vh-128px); }

@media (min-width: 1024px) {
  .grid {
    display: flex; } }

.grid .cell {
  flex-grow: 1; }

.grid .cell-large {
  flex-grow: 2; }

nav {
  position: fixed;
  top: 0px;
  background-color: #222222;
  width: 100%; }
  nav .hamburger-div {
    display: flex;
    height: 128px;
    width: 100%;
    margin-bottom: -128px;
    align-items: center; }
  nav .hamburger-icon {
    display: none; }
    @media (max-width: 645px) {
      nav .hamburger-icon {
        color: white;
        font-size: 40px;
        display: block;
        align-items: right;
        margin-left: 128px;
        border: 1px solid black;
        background-color: #191919;
        padding: 6.4px 10px; }
        nav .hamburger-icon:hover {
          color: black;
          background-color: #b8c2f8; } }
  nav .nav-btn a {
    color: white;
    text-decoration: none;
    padding: 6.4px 35.2px;
    border: 1px solid black;
    text-transform: lowercase;
    font-size: 2.3rem;
    background-color: #191919;
    margin-left: 16px; }
    nav .nav-btn a:hover {
      color: black;
      background-color: #b8c2f8; }
    @media (max-width: 645px) {
      nav .nav-btn a {
        display: none; } }
  nav ul {
    list-style: none;
    margin: 0;
    padding: 16px 0;
    display: flex;
    align-items: center; }
  nav .logo a {
    height: 96px;
    width: 96px;
    background-color: #101010;
    border-radius: 100%;
    box-shadow: 0px 0px 0px 3px black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 16px;
    transition: transform 2s ease-in-out; }
    nav .logo a:hover {
      box-shadow: 0px 0px 0px 3px #b8c2f8;
      transform: rotate(360deg); }
    nav .logo a img {
      height: 54.4px; }

.dropdown-content {
  display: none;
  position: absolute;
  top: 91px;
  left: 128px;
  background-color: #3c3c3c;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(124, 124, 124, 0.2);
  z-index: 1;
  border: 1px solid black; }
  .dropdown-content a {
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block; }
    .dropdown-content a:hover {
      background-color: #464646; }

footer {
  position: relative;
  bottom: 0px;
  background-color: #222222;
  height: 20vh;
  width: 100%; }
  footer svg {
    color: white; }

.computer {
  min-width: 30vw;
  max-width: 300px; }
  .computer:hover {
    filter: drop-shadow(0px 0px 20px #a5fcf6); }

.computerwrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  flex-flow: column nowrap; }

.comments p code {
  color: white;
  font-size: 2rem;
  margin: 0; }
  @media (max-width: 1023px) {
    .comments p code {
      font-size: 1rem; } }

.comments {
  padding: 8px;
  padding-top: 0; }

.v7domain {
  color: white;
  font-size: 5rem;
  text-align: center;
  margin: 0; }
  @media (max-width: 1023px) {
    .v7domain {
      font-size: 4.5rem; } }

.aboutmewrapper {
  min-height: calc(100vh-128px);
  width: 100%; }
  .aboutmewrapper .pfp {
    width: 320px;
    height: 320px;
    margin: 20.8px;
    border: 4px solid #1e1e1e; }
  .aboutmewrapper .skateboard {
    display: flex;
    margin: 20.8px; }
    .aboutmewrapper .skateboard .skateboardimg {
      max-height: 640px;
      max-width: 160px; }
    @media (max-width: 1023px) {
      .aboutmewrapper .skateboard {
        justify-content: center;
        align-items: center;
        max-height: 25vh; }
        .aboutmewrapper .skateboard .skateboardimg {
          transform: rotate(90deg);
          width: 25%; } }
  .aboutmewrapper .aboutbox {
    margin-top: 20.8px;
    margin-bottom: 20.8px;
    color: white;
    font-size: 1.5rem;
    line-height: 4rem;
    border: 1px solid white;
    padding: 16px;
    background-color: #323232; }
    @media (max-width: 1023px) {
      .aboutmewrapper .aboutbox {
        margin: 20.8px;
        line-height: 3rem; } }
    @media (max-width: 1500px) {
      .aboutmewrapper .aboutbox {
        line-height: 2.5rem; } }
    @media (max-width: 1210px) {
      .aboutmewrapper .aboutbox {
        font-size: 1.2rem;
        line-height: 2.3rem; } }
    @media (max-width: 1124px) {
      .aboutmewrapper .aboutbox {
        font-size: 1rem;
        line-height: 2.2rem; } }

/* width */
::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; }
